.footer {
  max-width: 880px;
  min-height: 78px;
  margin: 0 auto;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

@media screen and (min-width:581px) and (max-width: 879px) {
  .footer {
    max-width: 586px;
  }
}

@media screen and (max-width: 580px) {
  .footer {
    max-width: 280px;
    margin: 36px auto 0;
  }
}
