.form__input {
  font-family: inherit;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  border: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  max-width: 358px;
}

@media screen and (max-width: 580px) {
  .form__input {
    max-width: 238px;
    font-size: 14px;
  }
}
