.header {
  max-width: 880px;
  min-height: 74px;
  margin: 0 auto 40px;
  padding-top: 45px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 580px)  {
  .header {
    min-height: 56px;
    margin: 0 auto 42px;
    padding-top: 28px;
  }
}
