.elements {
  padding: 0;
  margin: 50px auto 66px;
  list-style: none;
  max-width: 880px;
  display: grid;
  gap: 17px;
  grid-template-columns: repeat(auto-fit, 282px);
  justify-content: center;
}

@media screen and (max-width: 580px) {
  .elements {
    margin: 36px auto 48px;
    gap: 20px;
  }
}
