.infoFoolTip {
  margin: 195px auto;
  padding: 60px 36px 60px;
  max-width: 430px;
  max-height: 430px;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

@media screen and (max-width: 580px) {
  .infoFoolTip {
    margin: 123px auto;
    padding: 25px 22px;
    width: 282px;
    min-height: 150px;
  }
}