.form-auth__input {
  margin: 0 auto 30px;
  padding-bottom: 13px;
  width: 358px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #CCCCCC;
  background-color: black;
  border: 0;
  border-bottom: 2px solid #CCCCCC;
}

.form-auth__input:focus {
  outline: none;
  border-bottom: 4px solid white;
}